import React, { useState } from 'react';
import axios from 'axios';
import LoginForm from "./LoginForm";

// Login component
const Login = ({ setToken }) => {
    const [formValues, setFormValues] = useState({ username: '', password: '' })

    // onSubmit handler
    const onSubmit = loginObject => {
        axios.post('/api/auth/login', loginObject)
            .then(res => {
                if (res.status === 200) {
                    if (res.data.valid) {
                        alert("Login successful!");
                        const token = loginUser(loginObject);
                        setToken(token);
                    }
                    else {
                        alert("Login unsuccessful. Please check your username and password and try again.");
                        Promise.reject();
                    }
                }
                else {
                    alert("Request not processed successfully. Status: " + res.status);
                    Promise.reject();
                }
            })
            .catch(err => alert('Something went wrong! ' + err));
    }

    function loginUser(loginObject) {
        return fetch('/api/auth/login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(loginObject)
        })
            .then(data => data.json())
    }

    // Return login page
    return(
        <LoginForm initialValues={formValues}
        onSubmit={onSubmit}
        enableReinitialize>
            Sign in
        </LoginForm>
    )
}

// Export component
export default Login